import React from "react";
import SubjectCard from "./SubjectCard";
const SubjectList = ({ subjects, carryParentState, isView }) => {
  return (
    <>
      {subjects?.map((sub) => (
        <SubjectCard
          sub={sub}
          key={sub?._id}
          carryParentState={carryParentState}
          isView={isView}
        />
      ))}
    </>
  );
};

export default SubjectList;
